import * as types from './products.types';

const initialState = {
  products: [
    {
      name: 'Iphone X',
      price: 1000,
      img:
        'https://lunafon.ru/image/cache/catalog/Telephones/Apple/apple-iphone-x-silver-1000x1000.jpg',
      description: 'new',
      id: '1',
    },
    {
      name: 'Meizu M8',
      price: 500,
      img:
        'https://i.citrus.ua/uploads/shop/8/2/8200a116bef8fd0ebe8e8f0f82ad708e.jpg',
      description: 'new',
      id: '2',
    },
    {
      name: 'Samsung',
      price: 900,
      img:
        'data:image/jpeg;base64,',
      description: 'new',
      id: '3',
    },
    {
      name: 'Huawei P40',
      price: 300,
      img:
        'https://encrypted-tbn0.gstatic.com/images?q=tbn%3AANd9GcQJIl8GSa2VDzknPyWSKtlQZ_JHs1Nus_4p32vuddJQ-0In9kV0kQ0OfJl0Hw&usqp=CAc',
      description: 'new',
      id: '4',
    },
  ],
};

const productsReducer = (state = initialState, action) => {
  switch (action.type) {
    case types.ADD_PRODUCT:
      return {
        ...state,
        products: [...state.products, action.payload],
      };
    case types.DELETE_PRODUCT:
      return {
        ...state,
        products: state.products.filter(
          (product) => product.id !== action.payload
        ),
      };
    default:
      return state;
  }
};

export default productsReducer;
